<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list v-if="shows.length!==0" class="ly-list">
      <template v-for="(show, index) of shows">
        <v-list-item :key="show[0]">
          <v-row no-gutters>
            <v-col cols="12" md="10">{{ show[1] }}</v-col>
            <v-col cols="6" md="2" offset="6" offset-md="0" class="text-right">
              <v-btn icon @click.stop="onDelClick(show, index)">
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon
                :to="{name: 'show_form', query: {mode: 'edit', id: show[0]}}"
              >
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==(shows.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row class="centered-row" v-if="(shows.length==0) && pageReady">
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>
            Belum ada data tayangan!
          </v-card-title>
          <v-card-text>
            Tambahkan tayangan dengan meng-klik tombol yang ada di sudut 
            kanan-bawah layar!
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn fab fixed bottom right dark color="pink" :disabled="loading"
      :to="{name: 'show_form', query: {mode: 'new'}}"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-dialog max-width="320" v-model="dlgConfirmDel">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Hapus {{ showNameToBeDel }}?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgConfirmDel=false" :disabled="loading">Batal</v-btn>
          <v-btn text color="warning" :loading="loading" @click="deleteShow">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { API_URL } from '@/config'
import { deleteWithAuth, getWithAuth } from '@/lib/fetch_utils'

export default {
  name: "Shows",
  data: () => ({
    loading: false,
    shows: [],
    dlgConfirmDel: false,
    showNameToBeDel: '',
    showIndexToBeDel: -1,
    pageReady: false
  }),
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Tayangan"})
    this.loadData()
  },
  methods: {
    loadData: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/shows`)
      this.loading = false
      this.pageReady = true
      if (200!==resp.code) {
        return
      }
      this.shows = resp.data.rows
    },
    onDelClick(show, index) {
      this.showIndexToBeDel = index
      this.showNameToBeDel = show[1]
      this.dlgConfirmDel = true
    },
    deleteShow: async function() {
      let showId = this.shows[this.showIndexToBeDel]
      this.loading = true
      let resp = await deleteWithAuth(`${API_URL}/shows/${showId}`)
      this.loading = false
      this.dlgConfirmDel = false
      if (200!==resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.shows.splice(this.showIndexToBeDel, 1)
    },
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}
.ly-list {
  margin-bottom: 64px;
}
</style>
