<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="12">
        <v-text-field label="Nama tayangan" v-model="show.name"></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-select label="Default playlist" :items="playlists"
          v-model="show.defaultPlaylist"
        ></v-select>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="6">
        Komposisi playlist
      </v-col>
      <v-col cols="6" class="text-right">
        <v-icon @click.stop="dlgSelectPlaylist = true">mdi-plus</v-icon>
      </v-col>
    </v-row>
    <v-list>
      <template v-for="(playlist, index) of show.layers">
        <v-list-item :key="index" two-line>
          <v-list-item-content>
            <v-list-item-title>{{ playlist.pn }}</v-list-item-title>
            <v-list-item-subtitle v-if="playlist.hasOwnProperty('sc')">
              {{ repr(playlist.sc) }}
            </v-list-item-subtitle>
            <v-list-item-subtitle class="text-right d-inline-block d-sm-none">
                <v-btn icon v-if="index!==(show.layers.length-1)"
                  @click="moveDownPlaylist(index)"
                >
                  <v-icon>mdi-arrow-down</v-icon>
                </v-btn>
                <v-btn icon v-if="index!==0" @click="moveUpPlaylist(index)">
                  <v-icon>mdi-arrow-up</v-icon>
                </v-btn>
                <v-btn icon @click="deletePlaylist(index)">
                  <v-icon>mdi-delete-forever</v-icon>
                </v-btn>
                <v-btn icon @click.stop="onBtnScheduleClick(index)">
                  <v-icon>mdi-clock-outline</v-icon>
                </v-btn>
            </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action class="d-none d-sm-block">
                <v-icon v-if="index!==(show.layers.length-1)"
                  @click="moveDownPlaylist(index)"
                >
                  mdi-arrow-down
                </v-icon>
                <v-icon v-if="index!==0" @click="moveUpPlaylist(index)">
                  mdi-arrow-up
                </v-icon>
                <v-icon @click="deletePlaylist(index)">mdi-delete-forever</v-icon>
                <v-icon @click.stop="onBtnScheduleClick(index)">mdi-clock-outline</v-icon>
          </v-list-item-action>
        </v-list-item>
        <v-divider :key="`_${index}`" v-if="index!==(show.layers.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row no-gutters>
      <v-col class="text-center">
        <v-btn color="primary" :disabled="loading" @click="$router.go(-1)">
          Batal
        </v-btn>
        &nbsp;
        <v-btn color="primary" :loading="loading" @click="onBtnSaveClick"
          :disabled="btnSaveDisabled"
        >
          Save
        </v-btn>
      </v-col>
    </v-row>
    <v-dialog max-width="360" v-model="dlgSelectPlaylist">
      <v-card>
        <v-list>
          <template v-for="(playlist, index) in playlists">
            <v-list-item :key="playlist.value" v-if="index!==0"
              @click="addPlaylist(index)"
            >
              {{ playlist.text }}
            </v-list-item>
            <v-divider :key="index" v-if="(index!==0) && (index!==(playlists.length-1))"></v-divider>
          </template>
        </v-list>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dlgSchedule" max-width="480" persistent
      @keydown.esc="dlgSchedule=false"
    >
      <v-card>
        <v-card-text>
          <v-row no-gutters>
            <v-col cols="4">
              <v-checkbox v-model="dateSpanOpt" label="Periode" hide-details></v-checkbox>
            </v-col>
            <v-col cols="4" v-show="dateSpanOpt">
              <v-menu v-model="menu1" ref="menu1"
                :return-value.sync="editedDateFrom"
                :close-on-content-click="false"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field v-model="computedDateFrom" label="Dari"
                    prepend-icon="mdi-calendar" readonly v-on="on" hide-details
                  ></v-text-field>
                </template>
                <v-date-picker v-model="editedDateFrom" no-title scrollable
                  locale="id-id"
                >
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="menu1 = false">Batal</v-btn>
                  <v-btn text color="primary"
                    @click="$refs.menu1.save(editedDateFrom)"
                  >Ok</v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="4" v-show="dateSpanOpt">
              <v-menu v-model="menu2" ref="menu2"
                :return-value.sync="editedDateTill"
                :close-on-content-click="false"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field v-model="computedDateTill" prepend-icon="mdi-calendar"
                    label="Sampai" readonly v-on="on" hide-details
                  ></v-text-field>
                </template>
                <v-date-picker v-model="editedDateTill" no-title scrollable
                  locale="id-id"
                >
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="menu2 = false">Batal</v-btn>
                  <v-btn text color="primary"
                    @click="$refs.menu2.save(editedDateTill)"
                  >Ok</v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="12">
              <v-checkbox v-model="specificDayOpt" label="Hari tertentu"
                hide-details
              ></v-checkbox>
            </v-col>
            <v-col cols="12">
              <v-radio-group v-model="spcDayGroup" v-show="specificDayOpt"
                hide-details
              >
                <v-row no-gutters>
                  <v-col cols="11" offset="1">
                    <v-radio label="Mingguan" value="0"></v-radio>
                  </v-col>
                  <v-col cols="7" offset="2" v-show="spcDayGroup==='0'">
                    {{ editedWeekdaysStr }}
                  </v-col>
                  <v-col cols="3" lass="text=right" v-show="spcDayGroup==='0'">
                    <v-menu v-model="menu5" ref="menu5"
                      :close-on-content-click="false"
                    >
                      <template v-slot:activator="{ on }">
                        <v-icon v-on="on" color="primary">mdi-pencil</v-icon>
                      </template>
                      <weekday-select v-bind:weekdays-data="editedWeekdays"
                        v-on:selectionData="onSelectedWeekdaysReceived"
                      ></weekday-select>
                    </v-menu>
                  </v-col>
                  <v-col cols="11" offset="1">
                    <v-radio label="Tiap tanggal" value="1"></v-radio>
                  </v-col>
                  <v-col cols="10" offset="2" v-show="spcDayGroup==='1'">
                    <v-text-field v-model="editedMonthDaysStr" hide-details
                      placeholder="Contoh: 3, 6, 29"></v-text-field>
                  </v-col>
                </v-row>
              </v-radio-group>
            </v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="4">
              <v-checkbox v-model="dailyOpt" label="Harian" hide-details></v-checkbox>
            </v-col>
            <v-col  cols="4" v-show="dailyOpt">
              <v-menu v-model="menu3" ref="menu3"
                :return-value.sync="editedTimeFrom"
                :close-on-content-click="false"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field v-model="editedTimeFrom" prepend-icon="mdi-clock-outline"
                    label="Awal" readonly v-on="on" hide-details
                  ></v-text-field>
                </template>
                <v-time-picker v-if="menu3" v-model="editedTimeFrom"
                  @click:minute="$refs.menu3.save(editedTimeFrom)"
                  format="24hr"
                ></v-time-picker>
              </v-menu>
            </v-col>
            <v-col cols="4" v-show="dailyOpt">
              <v-menu v-model="menu4" ref="menu4"
                :return-value.sync="editedTimeTo"
                :close-on-content-click="false"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field v-model="editedTimeTo" prepend-icon="mdi-clock-outline"
                    label="Akhir" readonly v-on="on" hide-details
                  ></v-text-field>
                </template>
                <v-time-picker v-if="menu4" v-model="editedTimeTo"
                  @click:minute="$refs.menu4.save(editedTimeTo)" format="24hr"
                ></v-time-picker>
              </v-menu>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="dlgSchedule=false">Batal</v-btn>
          <v-btn color="primary" @click="onOkDlgSchClick">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { API_URL } from '@/config'
import { getWithAuth, postWithAuth } from '@/lib/fetch_utils'
import { pad, WEEKDAYS_SHORT } from '@/lib/utils'
import WeekdaySelect from '@/components/WeekdaySelect'

function dateFormat(isoStrDate, format) {
  let x = isoStrDate.split('-')
  if (format === 0) return `${x[1]}/${x[2]}/${x[0]}`
  if (format === 1) return `${x[2]}/${x[1]}/${x[0]}`
  return `${x[1]}/${x[2]}/${x[0]}`
}

function secondSinceMidnightToStr(s){
  let _h = Math.floor(s/3600)
  let _m = Math.floor((s%3600)/60)
  let _hp = pad(_h)
  let _mp = pad(_m)
  return `${_hp}:${_mp}`
}

function daysStrToArray(s){
    let x = s.replace(/,/g, ' ')
    x = x.replace(/\s\s+/g, ' ')
    let y = x.split(' ')
    let z = []
    let p = 0
    let valid = true
    for (let i=0; i<y.length; i++) {
        p = parseInt(y[i])
        if (isNaN(p)) {
            valid = false
            break
        }
        if ((p<1) || (p>31)) {
            valid = false
            break
        }
        z.push(p)
    }
    if (valid) return z
    return null
}

function timeStrToSecSinceMidnight(s) {
    let x = s.split(':')
    let y = parseInt(x[0])
    if (isNaN(y)) return null
    let z = parseInt(x[1])
    if (isNaN(z)) return null
    return (3600*y) + (60*z)
}

function humanizeScheduleData(schedule){
    let buffStr = []
    let statements = []
    if (Object.prototype.hasOwnProperty.call(schedule, 'df')) {
        let _d = `Periode ${dateFormat(schedule.df, 1)} sampai ${dateFormat(schedule.dt, 1)}.`
        statements.push(_d)
    }
    if (Object.prototype.hasOwnProperty.call(schedule, 'sd')){
        if (schedule.sd === 1) {
            buffStr = []
            for (let j=0; j<schedule.wd.length; j++){
                buffStr.push(WEEKDAYS_SHORT[schedule.wd[j]])
            }
            if (buffStr.length > 1) {
                let _d = 'Tiap ' + buffStr.join(', ') + '.'
                statements.push(_d)
            } else {
                let _d = 'Tiap ' + buffStr[0] + '.'
                statements.push(_d)
            }
        } else if (schedule.sd === 2) {
            buffStr = []
            for (let j=0; j<schedule.md.length; j++){
                buffStr.push("" + schedule.md[j])
            }
            if (buffStr.length > 1) {
                let _d = 'Tiap tanggal: ' + buffStr.join(', ') + '.'
                statements.push(_d)
            } else {
                let _d = 'Tiap tanggal: ' + buffStr[0] + '.'
                statements.push(_d)
            }
        }
    }
    if (Object.prototype.hasOwnProperty.call(schedule, 'db')) {
        let _h1 = Math.floor(schedule.db/3600)
        let _m1 = Math.floor((schedule.db%3600)/60)
        let _h2 = Math.floor(schedule.de/3600)
        let _m2 = Math.floor((schedule.de%3600)/60)
        let _h1p = pad(_h1)
        let _m1p = pad(_m1)
        let _h2p = pad(_h2)
        let _m2p = pad(_m2)
        let _d = `Harian dari jam ${_h1p}:${_m1p} sampai ${_h2p}:${_m2p}.`
        statements.push(_d)
    }
    return statements.join(' ')
}

export default {
  name: "ShowForm",
  data: (vm) => ({
    show: {
      id: vm.$route.query.id || '',
      name: '',
      defaultPlaylist: 'sys_brand',
      layers: []
    },
    dailyOpt: false,
    dateSpanOpt: false,
    dlgSchedule: false,
    dlgSelectPlaylist: false,
    editedDateFrom: '',
    editedDateTill: '',
    editedLayerIndex: -1,
    editedMonthDays: [],
    editedMonthDaysStr: '',
    editedTimeFrom: '',
    editedTimeTo: '',
    editedWeekdays: [],
    loading: false,
    menu1: false,
    menu2: false,
    menu3: false,
    menu4: false,
    menu5: false,
    mode: vm.$route.query.mode || 'new',
    spcDayGroup: "0",
    specificDayOpt: false,
    playlists: [
      {
        text: "Tanpa default playlist",
        value: "sys_brand"
      },
      {
        text: "Matikan layar",
        value: "sys_screen_off"
      }
    ],
  }),
  components: {
    WeekdaySelect}
    ,
  computed: {
    formTitle() {
      if ('new'===this.mode) return 'Tayangan Baru'
      if ('edit'===this.mode) return 'Edit Tayangan'
      return 'Form Tayangan'
    },
    btnSaveDisabled() {
      if (this.show.name.length===0) return true
      if (this.loading) return true
      return false
    },
    editedWeekdaysStr(){
      let _x = []
      for (let i=0; i<this.editedWeekdays.length; i++) {
        _x.push(WEEKDAYS_SHORT[this.editedWeekdays[i]])
      }
      if (_x.length === 0) return 'Pilih hari!'
      return _x.join(', ')
    },
    computedDateFrom() {
      if (!this.editedDateFrom) return ""
      return dateFormat(this.editedDateFrom, 1)
    },
    computedDateTill() {
      if (!this.editedDateTill) return ""
      return dateFormat(this.editedDateTill, 1)
    }
  },
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title: this.formTitle, backButton: true})
    if ('edit'===this.mode) this.loadData()
    this.loadPlaylistSelectionItems()
  },
  methods: {
    loadData: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/shows/${this.show.id}`)
      this.loading = false
      if (200!==resp.code) return
      this.show.name = resp.data.name
      this.show.defaultPlaylist = resp.data.dfpl
      this.show.layers = JSON.parse(resp.data.layers)
      this.show.created = resp.data.created
      this.show.updated = resp.data.updated
    },
    loadPlaylistSelectionItems: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/playlists`)
      this.loading = false
      if (200!==resp.code) return
      this.playlists.splice(2, (this.playlists.length-2))
      resp.data.rows.forEach(playlist => {
        this.playlists.push({
          value: `user_${playlist[0]}`,
          text: playlist[1]
        })
      })
    },
    addPlaylist(index) {
      this.show.layers.push({
        "pl": this.playlists[index].value,
        "pn": this.playlists[index].text
      })
      this.dlgSelectPlaylist = false
    },
    onBtnSaveClick() {
      if ('edit'===this.mode) {
        this.commitUpdateShow()
      } else {
        this.commitAddNewShow()
      }
    },
    onOkDlgSchClick() {
      let hasError = false
      let errors = []
      let hasOptChecked = false
      if (this.dateSpanOpt) {
        hasOptChecked = true
        if (this.editedDateFrom === '') {
          hasError = true
          errors.push('date from')
        }
        if(this.editedDateTill === '') {
          hasError = true
          errors.push('date till')
        }
      }
      if (this.specificDayOpt) {
        hasOptChecked = true
        if (this.spcDayGroup === "0") {
          if (this.editedWeekdays.length === 0) {
            hasError = true
            errors.push('week days')
          }
        } else {
          let _d = daysStrToArray(this.editedMonthDaysStr)
          if (null === _d) {
            hasError = true
            errors.push('month days')
          }
        }
      }
      if (this.dailyOpt) {
        hasOptChecked = true
        if (this.editedTimeFrom === '') {
          hasError = true
          errors.push('time from')
        }
        if (this.editedTimeTo === '') {
          hasError = true
          errors.push('time to')
        }
      }
      if (hasError) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: 'ERROR: ' + errors.join(', ')
        })
        return
      }
      if (!hasOptChecked) {
        // Remove schedule data from playlist
        if (Object.prototype.hasOwnProperty.call(this.show.layers[this.editedLayerIndex], 'sc')){
          delete this.show.layers[this.editedLayerIndex].sc
        }
        this.dlgSchedule = false
        return
      }
      let data = {}
      if (this.dateSpanOpt) {
        data.df = this.editedDateFrom
        data.dt = this.editedDateTill
      }
      if (this.specificDayOpt) {
        if (this.spcDayGroup === "0") {
          data.sd = 1
          data.wd = this.editedWeekdays
        } else {
          data.sd = 2
          data.md = daysStrToArray(this.editedMonthDaysStr)
        }
      }
      if (this.dailyOpt) {
        data.db = timeStrToSecSinceMidnight(this.editedTimeFrom)
        data.de = timeStrToSecSinceMidnight(this.editedTimeTo)
      }
      this.show.layers[this.editedLayerIndex].sc = data
      this.dlgSchedule = false
    },
    moveDownPlaylist(index) {
      let _tmp = this.show.layers[index]
      this.show.layers.splice(index, 1)
      this.show.layers.splice(index+1, 0, _tmp)
    },
    moveUpPlaylist(index) {
      let _tmp = this.show.layers[index-1]
      this.show.layers.splice(index-1, 1)
      this.show.layers.splice(index, 0, _tmp)
    },
    deletePlaylist(index) {
      this.show.layers.splice(index, 1)
    },
    onBtnScheduleClick(index) {
      this.editedLayerIndex = index
      this.dateSpanOpt = false
      this.editedDateFrom = ''
      this.editedDateTill = ''
      this.specificDayOpt = false
      this.editedWeekdays = []
      this.editedMonthDays = []
      this.dailyOpt = false
      this.editedTimeFrom = ''
      this.editedTimeTo = ''
      if (Object.prototype.hasOwnProperty.call(this.show.layers[index], 'sc')) {
        if (Object.prototype.hasOwnProperty.call(this.show.layers[index].sc, 'df')) {
          this.dateSpanOpt = true
          this.editedDateFrom = this.show.layers[index].sc.df
          this.editedDateTill = this.show.layers[index].sc.dt
        }
        if (Object.prototype.hasOwnProperty.call(this.show.layers[index].sc, 'sd')) {
          this.specificDayOpt = true
          if (this.show.layers[index].sc.sd===1) {
            this.spcDayGroup = "0"
            this.editedWeekdays = this.show.layers[index].sc.wd
          } else {
            this.spcDayGroup = "1"
            this.editedMonthDays = this.show.layers[index].sc.md
          }
        }
        if (Object.prototype.hasOwnProperty.call(this.show.layers[index].sc, 'db')) {
          this.dailyOpt = true
          this.editedTimeFrom = secondSinceMidnightToStr(
            this.show.layers[index].sc.db
          )
          this.editedTimeTo = secondSinceMidnightToStr(
            this.show.layers[index].sc.de
          )
        }
      }
      this.editedMonthDaysStr = this.editedMonthDays.join(', ')
      this.dlgSchedule = true
    },
    repr(schedule) {
      return humanizeScheduleData(schedule)
    },
    onSelectedWeekdaysReceived(data) {
      if (null!==data){
        this.editedWeekdays = data
      }
      this.menu5 = false
    },
    commitAddNewShow: async function() {
      let data = {
        name: this.show.name,
        default_playlist: this.show.defaultPlaylist,
        layers: this.show.layers
      }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/shows`, data)
      this.loading = false
      if(200!==resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.$router.go(-1)
    },
    commitUpdateShow: async function() {
      let data = {
        name: this.show.name,
        default_playlist: this.show.defaultPlaylist,
        layers: this.show.layers
      }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/shows/${this.show.id}`, data)
      this.loading = false
      if(200!==resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.$router.go(-1)
    },
  }
}
</script>

<style scoped>

</style>
