<template>
  <v-card max-width="300">
    <v-card-text>
      <v-row no-gutters>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[0]" :label="weekdayNames[0]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[1]" :label="weekdayNames[1]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[2]" :label="weekdayNames[2]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[3]" :label="weekdayNames[3]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[4]" :label="weekdayNames[4]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[5]" :label="weekdayNames[5]"
            hide-details></v-checkbox>
        </v-col>
        <v-col cols="6">
          <v-checkbox v-model="weekdaysFlags[6]" :label="weekdayNames[6]"
            hide-details></v-checkbox>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn text color="primary" @click="onBtnCancelClick">Batal</v-btn>
      <v-btn text color="primary" @click="onBtnOkClick">Ok</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
'use strict'

import { WEEKDAYS } from '@/lib/utils'

export default {
  name: 'weekday-select',
  data(){
    return {
      loading: false
    }
  },
  computed: {
    weekdaysFlags() {
      let _x = [false, false, false, false, false, false, false]
      for (let i=0; i<7; i++) {
        _x[this.weekdaysData[i]] = true
      }
      return _x
    },
    weekdayNames() {
      return WEEKDAYS
    }
  },
  props: ['weekdaysData'],
  methods: {
    onBtnCancelClick(){
      this.$emit('selectionData', null)
    },
    onBtnOkClick(){
      let _data = []
      let _wfs = this.weekdaysFlags
      for (let i=0; i<7; i++){
        if (_wfs[i]){
          _data.push(i)
        }
      }
      this.$emit('selectionData', _data)
    }
  }
}
</script>
